import React, { ReactNode } from 'react';
import './Button.css';

interface ButtonProps {
  variant?:'primary' | 'secondary' | 'tertiary'
  primary?: boolean; 
  size?: 'small' | 'medium' | 'large';
  
  children: ReactNode;

  onClick?: () => void;
}


export const Button = ({
  variant ='primary',
  primary = false,
  size = 'medium',
  children,
  ...props
}: ButtonProps) => {

  
  switch (variant) {
    case "primary":
        return 'button--primary'
    case "secondary":
        return 'button--secondary'
    case "primary":
        return 'button--primary'

  
    default:
        break;
  }
  
  return (
    <button
      type="button"
      className={['button', `button--${size}`, variant].join(' ')}
      
      {...props}
    >
      {children}
    </button>
  );
};
